export default {
	data() {
		return {
			dateList: ['今天','明天','后天','两天后'],
			currIndex: 0,
			clickDate: '',
			banners: [],
			actList: [],
			inWhiteList: true,
			isTwoLater: false,
			lat: '30.57447',
			lon: '103.92377',
			isShowDiatance: false,
			showNoData: false
		}
	},
	created() {
		const that = this
		// 默认日期为当天日期
		this.clickDate = this.$utils.findDate(0)
		// 获取banner
		this.$http.get({
			 url: '/v1/rest/home/homeNewsList',
			 data: {
				 region:''
			 }
		}).then(resp => {
			console.log(resp)
			if(resp.status == 200) {
				this.banners = resp.data
			}
		})
	 // 获取用户地理位置
		uni.getLocation({
				type: 'wgs84',
				success: function (res) {
					console.log(res)
					that.lat = res.latitude
					that.lon = res.longitude
					that.isShowDiatance = true
					that.getList()
				 },
				fail: function (){
					that.getList()
					that.isShowDiatance = false
				}
		 });
	},
	onPullDownRefresh() {
		wx.setBackgroundColor({
		  backgroundColor: '#000000', // 窗口的背景色为白色
		})
		this.$http.post({
			url: '/v1/rest/home/homeActivitiesList',
			data: {
				isTwoDaysLater: this.isTwoLater,
				lat: this.lat,
				lon: this.lon,
				time: this.clickDate,
			}
		}).then(resp => {
			console.log(resp)
			if(resp.status == 200) {
				uni.stopPullDownRefresh()
				this.actList = resp.data
			}
		})
		// 获取banner
		this.$http.get({
			 url: '/v1/rest/home/homeNewsList',
			 data: {
				 region:''
			 }
		}).then(resp => {
			console.log(resp)
			if(resp.status == 200) {
				this.banners = resp.data
			}
		})
	},
	methods: {
		// 获取活动列表
		getList() {
			this.$http.post({
				url: '/v1/rest/home/homeActivitiesList',
				data: {
					isTwoDaysLater: this.isTwoLater,
					lat: this.lat,
					lon: this.lon,
					time: this.clickDate,
				}
			}).then(resp => {
				console.log(resp)
				if(resp.status == 200) {
					this.actList = resp.data
					if(resp.data.length == 0) {
						this.showNoData = true
					}else{
						this.showNoData = false
					}
				}
			})
		},
		// 切换日期
		changeDate(index) {
			this.showNoData = false
			this.currIndex = index
			switch(index) {
				case 0: 
					this.clickDate = this.$utils.findDate(0)
					this.isTwoLater = false
					this.getList()
					break;
				case 1: 
					this.clickDate = this.$utils.findDate(1)
					this.isTwoLater = false
					this.getList()
					break;
				case 2: 
					this.clickDate = this.$utils.findDate(2)
					this.isTwoLater = false
					this.getList()
					break;
				case 3: 
					this.clickDate = this.$utils.findDate(3)
					this.isTwoLater = true
					this.getList()
					break;
			}
		},
		// 点击轮播图
		clickBanner(item) {
			console.log(item)
			if (this.inWhiteList) { //如果在小程序的网址白名单中，会走内置webview打开，否则会复制网址提示在外部浏览器打开
				uni.navigateTo({
					url: '/pages/out/out?url=' + item.webUrl
				});
			} else {
				uni.setClipboardData({
					data: item.webUrl
				});
				uni.showModal({
					content: '本网址无法直接在小程序内打开。已自动复制网址，请在手机浏览器里粘贴该网址',
					showCancel: false
				});
			}
		},
		// 点击去报名
		toSignUp(id) {
			uni.navigateTo({
				url: '/pages/activity/activityDetails/activityDetails?actId=' + id
			})
		},
		// 俱乐部
		toClubId(item) {
			uni.navigateTo({
				url: '/pages/club/homePage/homePage?clubId=' + item.clubId
			})
		}
	}
}
